.regular-title {
  padding: 0;
  margin: 0;
  max-width: 568px;
  font-family: var(
    --font-headlines-family
  );
  font-style: normal;
  font-weight: 400;
  font-size: var(
    --font-headline-4-size
  );
  line-height: var(
    --font-headline-4-height
  );
  letter-spacing: var(
    --font-headlines-letter-spacing
  );
  color: var(--color-carbon);
}
